@import 'assets/scss/utils/mixins';

.container {
	max-width: 1000px;
}

.main-container {
	padding-top: 48px;
	padding-bottom: 48px;
}

.divider {
	height: 5px;
	background: var(--prim-2);
}

.divider-opacity {
	opacity: 0.15;
}

.divider-margin {
	margin-top: 72px;
	margin-bottom: 72px;
}

.w-34px {
	width: 34px;
}

.w-52px {
	width: 52px;
}

.w-100px {
	width: 100px;
}

.mt-48px {
	margin-top: 48px;
}

.mt-60px {
	margin-top: 60px;
}

.mx-60px {
	margin-top: 60px;
	margin-bottom: 60px;
}

.pb-56px {
	padding-bottom: 56px;
}

.connect-btn {
	max-width: 250px;
	height: 40px;
	padding: 0px 16px;
	background: var(--prim-2);
	border: none;
}

.section-content {
	max-width: 450px;
	min-width: 300px;
}

.tenantin-img {
	max-width: 423px;
	max-height: 265px;
}

.header-img {
	margin-top: 40px;

	.hero-header-img {
		max-width: 900px;
	}
}

.footer-section {
	margin-top: 72px;
	height: 280px;

	.img-content {
		width: 459px;
	}
}

.video-img {
	border-radius: 15px;
	filter: brightness(0.7);
}

.centered {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.horizontal-line {
	width: 100px;
	height: 5px;
	background-color: var(--prim-2);
	opacity: 15%;
	margin-top: 32px;
	margin-bottom: 32px;
}

.form-container {
	max-width: 600px !important;
}

.badge-text {
	padding-left: 8px;
	padding-right: 8px;
	background-color: var(--general-5);
	color: var(--basic-1);
	border-radius: 4px;
	font-size: 12px;
	font-weight: 500;
}

@include media-breakpoint-up(lg) {
	.h-lg-100 {
		height: 100%;
	}

	.border-lg {
		border: 1px solid var(--neutral-2) !important;
	}
}

@include media-breakpoint-down(md) {
	.divider-margin {
		margin-top: 40px;
		margin-bottom: 40px;
	}

	.footer-section {
		margin-top: 40px;
		margin-bottom: 40px;
	}

	.hero-header-img {
		max-height: 275px;
	}

	.video-img {
		width: 100%;
		height: 100%;
	}
}

.full-width-table {
	width: 100%;
	border-collapse: collapse;
}

td,
th {
	border: 1px solid var(--neutral-2) !important; /* Light grey border */
	padding: 16px 12px 16px 16px !important;
}

.deleted-text {
	text-decoration: line-through !important;
}

.advertise-container {
	@include media-breakpoint-up(md) {
		max-width: 1000px;
		margin-left: auto;
		margin-right: auto;
	}
}

.icon-15px {
	font-size: 15px;
	width: 15px;
	height: 15px;
}

.mt-40px {
	margin-top: 40px;
}

.subscribe-cursor {
	cursor: context-menu !important;
}
